.success-message {
  display: none;

  &--show {
    display: flex;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    justify-content: center;
    align-items: center;
    z-index: 200;
  }
}

.success-message__popup {
  position: relative;
  width: 728px;
  height: 444px;
  background-color: $light-background;
  font-weight: 500;
  font-size: 25px;
  line-height: 170%;
  color: $dark-grey;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.success-message__icon {
  width: 22px;
  height: 22px;
}

.success-message__close {
  top: 20px;
  right: 20px;
  position: absolute;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
