.page-header {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding: 14px 20px;

  @media (max-width: $tablet-width) {
    display: block;
    padding: 0;
  }
}

.no-js {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.page-header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media (max-width: $tablet-width) {
    height: 50px;
    padding-left: 21px;
  }

  @media (max-width: $mobile-width) {
    padding-left: 16px;
  }
}

.no-js .page-header__burger {
  display: none;
}

.page-header__burger {
  display: none;

  @media (max-width: $tablet-width) {
    display: block;
    padding: 10px 21px;
    background-color: $basic-accent;
    border: 0;
  }

  @media (max-width: $mobile-width) {
    padding: 10px 10px;
  }
}

.page-header__navigation {
  @media (max-width: $tablet-width) {
    position: absolute;
    top: 0;
    right: 0;
  }
}
